import { reformatDate } from '@/utils/chronos';
import { APP_BASE_URL } from '@/utils/constants';
import { downloadByUrl } from '@/utils/functions';
import AccessManager from '@/mixins/access-manager';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import { mapActions } from 'vuex';
export default {
  name: 'TablesBackup',
  mixins: [ViewsBaseMixin, AccessManager, SettingsMixin],
  data() {
    return {
      tablesList: [],
      tb: {
        tables: []
      },
      rules: {
        tables: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      isLoading: false,
      tbDialog: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.code.toLowerCase().includes(this.search.toLowerCase()) || data.insert_datetime.toLowerCase().includes(this.search.toLowerCase()));
    }
  },
  created() {
    this.init();
  },
  methods: {
    reformatDate,
    ...mapActions('data', ['getTablesList', 'getTablesBackups', 'makeTablesBackup', 'deleteTablesBackup']),
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    updateTableItemsInactivity() {
      this.tablesList.forEach((table, i) => {
        if (!this.tb.tables.length) {
          this.tablesList[i].inactive = false;
        } else {
          this.tablesList[i].inactive = table.value === '*' ? !this.tb.tables.includes('*') : this.tb.tables.includes('*');
        }
      });
    },
    showTables(tables) {
      if (tables) {
        let result = '';
        const tab = tables.split(';');
        for (let i = 0; i < tab.length; i++) {
          const table = this.tablesList.find(a => a.value == tab[i]);
          if (table) {
            result += table['label_' + this.$i18n.locale];
            if (i + 1 < tab.length) result += ' / ';
          }
        }
        return result;
      }
      return '';
    },
    async init() {
      this.isLoading = true;
      const resp0 = await this.getTablesList();
      if (resp0.success && resp0.data) this.tablesList = resp0.data;
      const resp = await this.getTablesBackups();
      if (resp.success) {
        if (resp.data) this.tableData = resp.data;
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
      this.isLoading = false;
    },
    addItem() {
      this.tb.tables = [];
      this.tbDialog = true;
    },
    downloadFile(index, item) {
      downloadByUrl(APP_BASE_URL + '/breeze-server/backups/' + item.code + '.sql');
    },
    deleteItem(index, item) {
      this.$confirm(this.$t('deleteBackupQuestion'), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.deleteTablesBackup(item.id);
        if (task.success) {
          this.$message.success(this.$t('success'));
          this.init();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    submitForm() {
      this.$refs.createTb.validate(async valid => {
        if (valid) {
          const mes = this.$message({
            message: this.$t('pleaseWait'),
            duration: 0
          });
          const task = await this.makeTablesBackup({
            code: this.$uuidv4(),
            tables: this.tb.tables.join(';')
          });
          mes.close();
          if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
          this.cancelForm();
          this.init();
        } else {
          return false;
        }
      });
    },
    cancelForm() {
      this.$refs.createTb.resetFields();
      this.tbDialog = false;
    }
  }
};