import "core-js/modules/es.array.push.js";
import { reformatDate } from '@/utils/chronos';
import { APP_BASE_URL } from '@/utils/constants';
import { capitalizeFirstLetter, downloadByUrl } from '@/utils/functions';
import AccessManager from '@/mixins/access-manager';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import { MEDIA_CONTEXT } from '@/store/modules/database/media';
import { mapActions } from 'vuex';
export default {
  name: 'Medias',
  mixins: [ViewsBaseMixin, AccessManager, SettingsMixin],
  data() {
    return {
      medias: [],
      formData: {
        mediasDate: ''
      },
      rules: {
        mediasDate: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      isLoading: false,
      dialog: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.code.toLowerCase().includes(this.search.toLowerCase()) || data.medias_date.toLowerCase().includes(this.search.toLowerCase()));
    },
    dates() {
      const d = [];
      this.medias.forEach(media => {
        const date = this.extractDateFormats(media.insert_datetime);
        if (date && d.findIndex(o => this.$_.isEqual(o, date)) == -1) {
          d.push(date);
        }
      });
      return d;
    }
  },
  created() {
    this.init();
  },
  methods: {
    reformatDate,
    ...mapActions('data', ['getMediasBackups', 'makeMediasBackup', 'deleteMediasBackup']),
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    extractDateFormats(datetime) {
      if (!datetime) return '';
      let date = reformatDate(datetime.split(' ')[0], {
        outPattern: 'DD MMMM YYYY',
        locale: this.$i18n.locale
      });
      date = date.split(' ');
      return {
        id: datetime.split(' ')[0],
        label: capitalizeFirstLetter(date[1]) + ' ' + date[2]
      };
    },
    async init() {
      this.isLoading = true;
      const resp0 = await this.getAllItemsFromDB({
        context: MEDIA_CONTEXT
      });
      if (resp0.success && resp0.data) this.medias = resp0.data;
      const resp = await this.getMediasBackups();
      if (resp.success) {
        if (resp.data) this.tableData = resp.data;
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
      this.isLoading = false;
    },
    addItem() {
      this.formData.mediasDate = '';
      this.dialog = true;
    },
    downloadFile(index, item) {
      downloadByUrl(APP_BASE_URL + '/breeze-server/backups/' + item.code + '.zip');
    },
    deleteItem(index, item) {
      this.$confirm(this.$t('deleteBackupQuestion'), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.deleteMediasBackup(item.id);
        if (task.success) {
          this.$message.success(this.$t('success'));
          this.init();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    submitForm() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          const mes = this.$message({
            message: this.$t('pleaseWait'),
            duration: 0
          });
          const task = await this.makeMediasBackup({
            code: this.$uuidv4(),
            medias_date: this.formData.mediasDate
          });
          mes.close();
          if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
          this.cancelForm();
          this.init();
        } else {
          return false;
        }
      });
    },
    cancelForm() {
      this.$refs.form.resetFields();
      this.dialog = false;
    }
  }
};